.flex { /*水平方向,项目从左到右排列,不可换行,垂直方向,从上到下*/
  display:flex;
  flex-flow: row nowrap;
}
.row_column_center {
  /*水平方向，水平居中，上下居中*/
  display: flex;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: center;
}

.row_between_wrap_column_center {
  /*水平方向，可换行, 项目垂直居中，项目两端对齐*/
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}

.row_between_nowrap_column_center {
  /*水平方向，不可换行, 项目垂直居中，项目两端对齐*/
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
}

.row_start_column_center { /* 水平方向，项目垂直居中，项目从左到右排列*/
