  align-items: center;
  flex-flow: row wrap;
}

.row_start_column_center_nowrap { /* 水平方向，项目垂直居中，项目从左到右排列*/
  display: flex;
  align-items: center;
  flex-flow: row wrap;
}
.row_end_column_center { /* 水平方向，项目垂直居中，项目从右到左排列*/
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-flow: row wrap;
}

.column_row_center {
  /* 垂直方向，两端对齐 中间对齐 */
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
